.nav-link a {
    color: #fff;
    text-decoration: none;
}

.nav-link a:hover {
    color: #fff;
    opacity: 0.8;
    text-decoration: none;
}

.nav-search {
    display: none;
}

@media all and (max-width: 991px) {
    .nav-search {
        display: block;
    }

    .nav-search .search {
        display: block;
    }
}